import { useEffect, useState } from "react";
import { Document, Page, pdfjs } from "react-pdf";
import { useRouter } from "next/router";
import { Button, Col, Row, Spin, Typography } from "antd";
import { CTitle } from "@components";
import CApi from "@api";

const { Text, Title } = Typography;
const FileAttachments = () => {
  const [pdfFile, setPdfFile] = useState("");
  const [loading, setLoading] = useState(true);
  const [numPages, setNumPages] = useState(null);
  const [fileName, setFileName] = useState(1);
  const router = useRouter();
  useEffect(() => {
    pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;
    getData();
  }, []);

  const getData = async () => {
    let id = router.query.id;
    setLoading(true);
    await CApi({
      method: "GET",
      url: "api/detail-file-attachment/" + id,
    })
      .then((res) => {
        setFileName(res.data.name_file);
        setPdfFile(res.data.type + res.data.file);
        setLoading(false);
      })
      .catch((_err) => {
        router.back();
      });
  };
  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
  }

  if (loading) {
    return (
      <Row
        style={{
          textAlign: "center",
          marginTop: "40vh",
          width: "100%",
          justifyContent: "center",
        }}
      >
        <Spin />
        <Row
          style={{
            marginTop: "10px",
            textAlign: "center",
            width: "100%",
            justifyContent: "center",
          }}
        >
          <Text>Loading File....</Text>
        </Row>
      </Row>
    );
  }

  const downloadFile = async () => {
    await fetch(pdfFile)
      .then((res) => res.blob())
      .then((res) => {
        const blobUrl = URL.createObjectURL(res);
        var a = document.getElementById("blob");
        a.download = fileName;
        a.href = blobUrl;
        a.click();
        a.href = "";
        a.download = "";
      });
  };

  return (
    <>
      <Row>
        <CTitle />
        <Row
          style={{
            textAlign: "center",
            width: "100%",
          }}
        >
          <Col span={24}>
            <Title level={3}>{`File Name : ${fileName}`}</Title>
          </Col>
          <Col
            span={24}
            style={{
              textAlign: "center",
            }}
          >
            <Button
              onClick={downloadFile}
              style={{ backgroundColor: "#1890ff", marginBottom: 15 }}
            >
              Download PDF
            </Button>
          </Col>
        </Row>
        <Col
          span={24}
          style={{
            textAlign: "center",
            width: "100%",
          }}
        >
          <Row
            style={{
              width: "100%",
              justifyContent: "center",
            }}
          >
            <Document file={pdfFile} onLoadSuccess={onDocumentLoadSuccess}>
              {Array.from(new Array(numPages), (el, index) => (
                <Page
                  key={`page_${index + 1}`}
                  pageNumber={index + 1}
                  pageIndex={index + 1}
                />
              ))}
            </Document>
          </Row>
        </Col>
        <a
          id="blob"
          style={{
            display: "none",
          }}
        />
      </Row>
    </>
  );
};
export default FileAttachments;
